import { Barcode } from 'src/app/models/barcode';
import { Order } from 'src/app/models/order';
import { OrderService } from 'src/app/services/order.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-orders-for-driver',
  templateUrl: './orders-for-driver.component.html',
  styleUrls: ['./orders-for-driver.component.scss'],
})
export class OrdersForDriverComponent implements OnInit {
  constructor(private orderService: OrderService) {}

  orders: Order[];
  displayedColumns: string[] = ['Склад', 'ШК', 'Номенклатура', 'Количество'];

  ngOnInit(): void {
    const data = JSON.parse(localStorage.getItem('user'));
    const id = data.uid;
    this.getOrders(id);
  }

  getOrders(id) {
    this.orderService.getOrdersByDriverUid(id).subscribe((order) => {
      this.orders = order;
    });
  }


}
